<template>
    <el-table :data="all_commande.filter(data => !search || data.code.toLowerCase().includes(search.toLowerCase()))" style="width: 100% cursor:pointer">

        <el-table-column label="status" width="120">
            <template slot-scope="scope">
                <el-tag v-if="scope.row.status == 0" type="info" size="medium">{{ scope.row.status_t }}</el-tag>
                <el-tag v-if="scope.row.status == 1" size="medium">{{ scope.row.status_t }}</el-tag>
                <el-tag v-if="scope.row.status == 2" type="success" size="medium">{{ scope.row.status_t }}</el-tag>
            </template>
        </el-table-column>
        <el-table-column label="Code" prop="code">
        </el-table-column>
        <el-table-column label="Client" prop="nom_client">
        </el-table-column>
        <el-table-column label="Prix total" prop="prix_total">
        </el-table-column>
        
        <el-table-column label="Auteur" prop="name">
        </el-table-column>
        <el-table-column label="Date de creation" prop="created_at">
        </el-table-column>

        <el-table-column align="right">
            <!-- <template slot="header" slot-scope="scope"> -->
            <template slot="header">
                <el-input  v-model="search"  size="mini" placeholder="Type to search"/>
            </template>
            <template slot-scope="scope">
                <div class="row mr-2">
                    <el-select class="col" v-model="scope.row.status" :change="handleChangeState(scope.row)" placeholder="Status" size="small"><el-option v-for="item in optionStatus" :key="item.value" :label="item.label" :value="item.value" :disabled="item.disabled"> </el-option></el-select>
                </div>
            </template>
        </el-table-column>

    </el-table>
</template>

<script>

import axios from 'axios';
// import moment from 'moment';
import NProgress from 'nprogress';

export default {
    
    props: ['all_commande', 'loadConfig'],

    data(){

        return {

            search: '',
            optionStatus:[
                {
                    label: 'En cours',
                    value: 0,
                },
                {
                    label: 'En attente de recuperation',
                    value: 1
                },
                {
                    label: 'Terminer',
                    value: 2
                }
            ],

        }
            
    },

    methods: {

        handleChangeState(data){
           
            NProgress.start();

            this.all_commande.forEach(element => {

              if (element.id == data.id) {
                if (element.status_active === data.status) {
                  console.log("equal");
                }
                else {
                  console.log("update");
                  this.updateCommande(data);
                }
              }
            });

            NProgress.done();

        },

        showRow(data) {
            console.log(data);
        },

        async updateCommande(data){


            let commande = {
                id : data.id,
                status: data.status,
            };

            NProgress.start();
            try {

                await axios.post('xpress/commande/updateState', commande);
                this.$notify({
                    type: 'success',
                    title: "Enregistrer"
                });

                this.loadConfig();

            } 
            catch (error) {
                    this.$notify({
                    type: 'error',
                    title: "Echec d'enregistrement"
                });
            }
            NProgress.done();
        }
    },


}
</script>
