<template>
    <div class="m-5">

    
        <div class="row" >
            <div class="col-xl-3 col-lg-4 col-md-5 col-sm-6 col-xs-6 mb-1">
                
                    <label>{{periode}} : </label>
                    <div class="data-picker" style="width:100%">
                        <el-date-picker
                            v-model="drp"
                            type="daterange"
                            unlink-panels
                            range-separator="-"
                            format="dd/MM/yyyy"
                            :clearable="clearable"
                            @change="loadFilter()"
                            :picker-options="pickerOptions">
                        </el-date-picker>
                    </div>
            </div>
            <div class="col-xl-3 col-lg-3 col-md-3 col-sm-3 col-xs-3 margin-bottom">
                <label>Choisir le status : </label>
                <div class="data-picker" style="width:100%">
                    <ui-select
                        placeholder="Choisir une usine"
                        @change="loadConfig()"
                        :options="status"
                        v-model="statusSelect"
                    ></ui-select>
                </div>
            </div>
            <div class="col-xl-3 col-lg-3 col-md-3 col-sm-3 col-xs-3 margin-bottom ml-2">
                <label>Auteur : </label>
                <div class="data-picker" style="width:100%">
                    <ui-select
                        placeholder="Choisir une usine"
                        :options="usersList"
                        @change="loadConfig()"
                        v-model="usersSelect"
                    ></ui-select>
                </div>
            </div>
        </div>

        <v-divider class="mt-5"></v-divider>

        <div class="card mt-3" v-if="(all_commande.length > 0) && (loading == false)">
            <TableList v-bind:all_commande="all_commande" v-bind:loadConfig="loadFilter" v-bind:type="type"/>
        </div>
        <div class="mt-3">   
            <template>
                <el-skeleton :loading="loading" animated :rows="6" />
            </template>
        </div>
        <div class="resultat" v-if="(all_commande.length == 0 || all_commande.length == '' || all_commande.length == ' ') && (loading == false)">
                <el-empty description="Pas de données"></el-empty>
        </div>

    </div>
</template>

<script>

import axios from 'axios';
import moment from 'moment';
import NProgress from 'nprogress';
import TableList from '@/components/dashboard/orders/TableList.vue';


export default {

    components: {
        TableList,
    },
    data() {
        return {
            type: 'periode',

            is_done : 0,
            status: [],
            usersList: [],
            usersSelect: '',
            statusSelect: '',

            periode: 'Choisir la periode',
            start:  moment().subtract(12, 'month').startOf('month').toDate(),
            end: moment().toDate(),
            clearable: true,
            //drp : [moment().subtract(200, 'isoweek').startOf('isoweek').toDate(), moment().subtract(1, 'isoweek').endOf('isoweek').toDate()],
            drp : [moment().subtract(1, 'isoweek').startOf('isoweek').toDate(), moment().toDate()],
            pickerOptions: {
                shortcuts: [{
                    text: 'Mois precedent',
                    onClick(picker) {
                        picker.$emit('pick', [moment().subtract(1, 'month').startOf('month').toDate(), moment().subtract(1, 'month').endOf('month').toDate()]);
                    }
                }, 
                {
                    text: 'Mois en cours',
                    onClick(picker) {
                        picker.$emit('pick', [moment().startOf('month').toDate(), new Date()]);
                    }
                }, 
                {
                    text: 'Semaine precedente',
                    onClick(picker) {
                        picker.$emit('pick', [moment().subtract(1, 'week').startOf('isoWeek').toDate(), moment().subtract(1, 'week').endOf('isoWeek').toDate()]);
                    }
                }, 
                {
                    text: 'Semaine en cours',
                    onClick(picker) {
                        picker.$emit('pick', [moment().startOf('isoWeek').toDate(), new Date()]);
                    }
                }, 
                {
                    text:'Avant hier',
                    onClick(picker) {
                        picker.$emit('pick', [moment().subtract(2, 'day').toDate(), moment().subtract(2, 'day').toDate()]);
                    }
                }, 
                {
                    text: 'Hier',
                    onClick(picker) {
                        picker.$emit('pick', [moment().subtract(1, 'day').toDate(), moment().subtract(1, 'day').toDate()]);
                    }
                }, {
                    text: 'Aujoudhuit',
                    onClick(picker) {
                        picker.$emit('pick', [moment().toDate(), new Date()]);
                    }
                }],
                format:'dd MM yyyy',
                firstDayOfWeek: 1,
                disabledDate: this.disabledDueDate
            },

            loading: false,
            
            all_commande: [],
        }
    },

    created() {
        moment.locale('fr');
        this.loadFilter();
    },


    methods: {
        disabledDueDate(date) {   
            return !(date >= this.start && date <= this.end)
        },

        async loadFilter()
        {
            NProgress.start();

            try {
                let crm = {
                    start: moment(new Date(this.drp[0])).startOf('day').format('YYYY-MM-DD HH:mm:ss.SSS'),
                    end: moment(new Date(this.drp[1])).endOf('day').format('YYYY-MM-DD HH:mm:ss.SSS'),
                };



                const response = await axios.post('xpress/commande/getFilter', crm);
                var res = response.data.users;
                var status = [];
                var tab_status = [];
                var tab = [];
                var name = [];
                let s = '';



                res.forEach(element => {
                    

                    if (!name.includes(element.name)) {
                        name.push(element.name);
                        tab.push({
                            label: element.name,
                            value: element.id
                        });
                    }



                    if (element.status == 0) {

                        s = 'En cours';
                        if (!status.includes(s)) {
                            status.push(s);
                            tab_status.push({
                                label: s,
                                value: element.status
                            });
                        }
                        
                    } else if (element.status == 1) {

                        s = 'En attente de Recuperation';
                        if (!status.includes(s)) {
                            status.push(s);
                            tab_status.push({
                                label: s,
                                value: element.status
                            });
                        }
                        
                    } else if (element.status == 2) {

                        s = 'Terminer';
                        if (!status.includes(s)) {
                            status.push(s);
                            tab_status.push({
                                label: s,
                                value: element.status
                            });
                        }
                    }

                });

                this.usersList = tab;
                this.usersList.unshift({
                    label: 'Tous',
                    value: 0
                });
                this.usersSelect = this.usersList[0];

                this.status = tab_status;

                this.status.unshift({
                    label: 'Tous',
                    value: 4
                });

                this.statusSelect = this.status[0];

                 this.loadConfig();
                
            } catch (error) {
                this.$notify({
                  type: 'error',
                  title: "Echec de recuperation"
              });
            }

            NProgress.done();

        },

        async loadConfig() 
        {
            // Start loading
            this.loading = true;
            NProgress.start();
            
            let crm = {
                start: moment(new Date(this.drp[0])).startOf('day').format('YYYY-MM-DD HH:mm:ss.SSS'),
                end: moment(new Date(this.drp[1])).endOf('day').format('YYYY-MM-DD HH:mm:ss.SSS'),
                status: this.statusSelect['value'],
                user: this.usersSelect['value'],

            };

            console.log(crm);

            const response = await axios.post('/xpress/commande/allPressingCmdUser', crm);
            
            // End of skeleton loading
            this.loading = false;

            var commandes = response.data.commande;

            commandes.forEach(element => {
                if (element.status == 0) {
                    element['status_t']= 'en cours';
                }
                else if (element.status == 1) {
                    element['status_t']= 'En attente de R';
                }
                else if (element.status == 2) {
                    element['status_t']= 'Terminer';
                }

                element['status_active'] = element.status;
                element.created_at = moment(new Date(element.created_at)).startOf('day').format('DD/MM/YYYY HH:mm:ss')
                console.log(element.created_at);
            });

            this.all_commande = commandes;
            this.all_commande_detail = response.data.commande_details;

            NProgress.done();
        },

        handleChangeState(data){
           
            NProgress.start();

            this.all_commande.forEach(element => {

              if (element.id == data.id) {
                if (!element.status_active === data.status) {
                    console.log("update");
                    this.updateCommande(data);
                }
              }
            });

            NProgress.done();

        },

        async updateCommande(data){
            
            NProgress.start();
            try {

                await axios.post('xpress/commande/update', data);
                this.$notify({
                    type: 'success',
                    title: "Enregistrer"
                });

            } 
            catch (error) {
                    this.$notify({
                    type: 'error',
                    title: "Echec d'enregistrement"
                });
            }
            NProgress.done();
        }
    }
}
</script>

<style>
  .input {
    display: inline-block;
    width: 25%;
  }
  .resultat {
    width: 100%;
  }
</style>